@charset "utf-8";

@import "basic/reset";
@import "basic/basic";

$accent-color: #ff8a00;
$gotham: "GothamPro", sans-serif;
$oswald: "Oswald", sans-serif;

@import "blocks/header";

.first-screen {
  position: relative;
  padding-bottom: 500px;
  background: url(../img/bg/bg1.jpg) no-repeat top center / cover;
  &__inner {
    margin: 0 auto;
    max-width: 1110px;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.55);
    border-radius: 10px;
    padding: 35px 60px;
    position: relative;
  }
  &__content {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 570px;
  }
  &__title {
    text-shadow: 2px 4px 5px rgba(0,0,0,0.33);
    font-family: $oswald;
    font-size: 48px;
    letter-spacing: 0.025em;
    color: #13560d;
    text-align: center;
    margin-bottom: 60px;
  }
  &__features {
    margin-bottom: 65px;
  }
  &__features-item {
    text-shadow: 2px 4px 5px rgba(0,0,0,0.33);
    font-size: 28px;
    color: #107106;
    margin-bottom: 20px;
    padding-left: 70px;
    position: relative;
    &::before {
      content: '';
      display: block;
      width: 50px;
      height: 50px;
      position: absolute;
      left: 0;
      bottom: -10px;
    }
    &:nth-of-type(1)::before {
      background: url(../img/icon/herbs.svg) no-repeat center / contain;
    }
    &:nth-of-type(2)::before {
      background: url(../img/icon/baby.svg) no-repeat center / contain;
    }
    &:nth-of-type(3)::before {
      background: url(../img/icon/truck.svg) no-repeat center / contain;
    }
  }
  &__btn {
    & a {
      padding: 20px 100px;
    }
  }
  &__img {
    position: absolute;
    z-index: 1;
    right: 0;
    top: 70px;
    animation: cornMoving 3s ease infinite alternate;
  }
}

@keyframes cornMoving {
  0% {
    transform: translateY(0px);
  }
  100% {
    transform: translateY(20px);
  }
}

.catalog {
  padding: 80px 0;
  &__inner {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-left: 25px;
    padding-right: 25px;
  }
  &__title {
    text-align: center;
    max-width: 500px;
    margin-bottom: 90px;
  }
}

.catalog-list {
  display: flex;
  justify-content: space-between;
  &__item {
    background-color: #fff;
    flex-basis: calc(25% - 9px);
    box-shadow: 0px 2px 4px 1px rgba(0, 0, 0, 0.4);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 20px;
  }
  &__img {
    min-height: 250px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 15px;
    & > img {

    }
  }
  &__desc {

  }
  &__title {
    font-size: 18px;
    letter-spacing: 0.025em;
    text-align: center;
    margin-bottom: 20px;
  }
  &__text {
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 0.025em;
    text-align: center;
    display: flex;
    flex-direction: column;
    padding: 0 15px;
  }
}

.form-pricing {
  background-color: #fff;
  width: 520px;
  margin-bottom: 160px;
}

.features {
  margin-bottom: 230px;
  overflow: hidden;
  &__inner {
    padding-left: 65px;
    padding-right: 65px;
  }
  &__title {
    margin-bottom: 30px;
  }
  &__content {

  }
}

.features-content {
  display: flex;
  &__circle {
    width: 360px;
    height: 360px;
    border-radius: 50%;
    margin-top: 70px;
    padding-top: 30px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    position: relative;
    background-position: center;
    background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3ccircle r='172' cx='180' cy='180' fill='none' stroke='%2382828280' stroke-width='4' stroke-dasharray='5%25%2c 5%25' stroke-dashoffset='0' stroke-linecap='square'/%3e%3c/svg%3e");
    & span {
      text-shadow: 3px 5px 5px rgba(0,0,0,0.18);
      font-size: 28px;
      text-align: center;
    }
    &::after, &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      border: 1px solid rgba(130, 130, 130, 0.5);
    }
    &::before {
      animation: pulse 8s ease infinite;
    }
    &::after {
      animation: pulse 8s 3s ease infinite;
    }
  }
  &__col {
    display: flex;
    flex-direction: column;
  }
  &__item {
    position: relative;
    display: flex;
    align-items: center;
    & span {
      text-shadow: 3px 5px 5px rgba(0,0,0,0.18);
      font-size: 28px;
      max-width: 450px;
    }
    & img {}
    &:nth-of-type(2) {
      // left: -100px;
    }
    &:nth-of-type(3) {
      left: -10px;
    }
    &:nth-of-type(4) {
      left: -110px;
    }
  }
}
@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 0;
  }
  5% {
    opacity: 1;
  }
  60% {
    opacity: 0.7;
  }
  100% {
    transform: scale(2);
    opacity: 0;
  }
}

.we-work {
  margin-bottom: 200px;
  &__inner {
    padding-left: 40px;
    padding-right: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title {
    text-align: center;
    margin-bottom: 90px;
  }
  &__row {}
}

.we-work-list {
  &__item-container {

  }
  &__item {
    height: 100%;
    border: 1px solid #898989;
    border-radius: 10px;
    background-color: #fff;
    position: relative;
    z-index: 2;
    display: flex;
    align-items: center;
    flex-direction: column;
    &::after {
      content: '';
      display: block;
      position: absolute;
      z-index: -1;
      left: 17px; right: 17px; top: 17px; bottom: 17px;
      border: 1px solid #898989;
      border-radius: 10px;
    }
    &::before {
      content: attr(data-count);
      position: absolute;
      top: 0;
      opacity: 0.07;
      font-family: $oswald;
      font-size: 300px;
      color: #000000;
    }
  }
  &__img {
    min-height: 210px;
    width: 100%;
    background: no-repeat center 40% / auto;
  }
  &__title {
    text-shadow: 3px 5px 5px rgba(0,0,0,0.18);
    font-size: 24px;
    margin-bottom: 15px;
    text-align: center;
  }
  &__text {
    padding: 0 30px 30px;
    text-align: center;
    text-shadow: 3px 5px 5px rgba(0,0,0,0.18);
    font-size: 16px;
  }
}

.certificates {
  margin-bottom: 100px;
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-left: 100px;
    padding-right: 100px;
  }
  &__title {
    text-align: center;
    margin: 0 auto;
    margin-bottom: 90px;
  }
  &__content {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: stretch;
    background: url(../img/bg/bg5.png) no-repeat bottom center / contain;
    padding-bottom: 100px;

  }
  &__item {
    margin-left: 30px;
    margin-right: 30px;
    position: relative;
    display: flex;
    justify-content: stretch;
    align-items: stretch;
    padding: 3% 2%;
    & a {
      display: flex;
      justify-content: stretch;
      align-items: stretch;
    }
    & img {
      max-height: 410px;
    }
    & > a::after {
      content: '';
      background: url(../img/content/frame.png) no-repeat center / 100% 100%;
      display: block;
      position: absolute;
      z-index: 5;
      left: 0; right: 0; top: 0; bottom: 0;
    }
  }
}

.faq {
  background: url(../img/bg/bg6.png) no-repeat center / cover;
  padding-top: 350px;
  padding-bottom: 100px;
  &__inner {
    padding-left: 15px;
    padding-right: 15px;
    display: flex;
    justify-content: space-between;
  }
  &__questions {
    flex-basis: 52%;
    margin-right: 55px;
  }
  &__title {
    font-size: 36px;
    line-height: 36px;
    color: #f9f7f7;
    margin-bottom: 25px;
  }
  &__form {
    flex-basis: 45%;
  }
}

.questions-list {
  &__item {
    cursor: pointer;
    margin-bottom: 30px;
    background-color: #fafaf9;
    border-radius: 10px;
    min-height: 65px;
    padding: 25px;
    box-shadow: 2px 4px 5px 0px rgba(0,0,0,0.15);
    position: relative;
    &::after {
      content: '';
      background: url(../img/icon/down-arrow.svg) no-repeat center / cover;
      display: block;
      width: 37px;
      height: 24px;
      position: absolute;
      right: 20px;
      top: 22px;
      transition: .4s;
    }
    &--active::after {
      transform: rotate(-180deg);
    }
  }
  &__answer {
    padding-top: 20px;
    display: none;
    &--shown {
      display: block;
      animation: showanswer .5s ease;
    }
  }
}

@keyframes showanswer {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@import "blocks/footer";


.thanks-page-wrapper {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  min-height: 100vh;
  & .header {
    color: #575757;
  }
  & .burger-btn {
    span, span::before, span::after {
      background-color: #000000;

    }
  }
}
.thanks {
  background: url(../img/bg/bg6.png) no-repeat center;
  background-position-y: -150px;
  background-size: 100% 130%;
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  &__inner {
    padding-left: 120px;
    padding-right: 120px;
  }
  &__content {
    background: url(../../img/bg/frame.svg) no-repeat 0 0;
    background-size: 100% 100%;
    border-radius: 32px;
    box-shadow: 2px 3px 5px 0px rgba(144,85,1,0.36);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px 40px 200px;
    position: relative;
  }
  &__title {
    text-shadow: 2px 4px 5px 0px rgba(0,0,0,0.33);
    font-size: 36px;
    color: #ffffff;
    margin-bottom: 15px;
    max-width: 330px;
    text-align: center;
  }
  &__icon {
    background: url(../img/icon/tick.svg) no-repeat center / cover;
    width: 90px;
    height: 90px;
    margin-bottom: 15px;
  }
  &__text {
    text-shadow: 2px 4px 5px 0px rgba(0,0,0,0.33);
    font-size: 24px;
    color: #ffffff;
    text-align: center;
  }
  &__img {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 50%);
  }
}