$accent-color: #ff8a00;
$gotham: "GothamPro", sans-serif;
$oswald: "Oswald", sans-serif;

body {
  font-size: 16px;
  font-family: $gotham;
  &.lock {
    overflow: hidden;
  }
}
.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  // padding-left: 15px;
  // padding-right: 15px;
}
.title {
  font-size: 60px;
  font-family: $oswald;
  text-transform: uppercase;
  position: relative;
  z-index: 2;
  &::before {
    content: url(../../img/content/abstract.png);
    position: absolute;
    z-index: -1;
    bottom: 30px;
    left: -25px;
  }
}

.btn {
  color: #fff;
  font-size: 36px;
  font-family: $oswald;
  padding-top: 5px;
  padding-bottom: 8px;
  overflow: hidden;
  border: none;
  cursor: pointer;
  position: relative;
  text-align: center;
  transition: .15s;
  background-image: linear-gradient(0deg, $accent-color 0%, #ffe7ac 100%);
  box-shadow: 2px 3px 5px 0px rgba(144,85,1,0.85);
  border-radius: 10px;
  display: block;
  &::after {
    content: '';
    background-image: 
      linear-gradient(0.25turn, 
        rgba(224, 224, 224, 0.4), 
        rgba(224, 224, 224, 0.8), 
        rgba(224, 224, 224, 0.4));
    display: block;
    height: 100%;
    width: 80px;
    position: absolute;
    top: 0;
    left: -200px;
    transform: translateX(0) skew(-30deg);
    animation: btnFlare 3s ease infinite;
  }
  &:hover {
    background-image: linear-gradient(0deg, #ffe7ac 0%, $accent-color 100%);
  }
}

@keyframes btnFlare {
  0% {
    transform: translateX(0) skew(-30deg);
  }
  40% {
    transform: translateX(0) skew(-30deg);
  }
  60% {
    transform: translateX(900px) skew(-30deg);
  }
  100% {
    transform: translateX(900px) skew(-30deg);
  }
}

input:focus::placeholder {
  opacity: 0;
}

.socials {
  display: flex;
  align-items: center;
  &__item {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: inline-block;
    margin-left: 10px;
    transition: .25s;
    &:hover {
      opacity: .7;
    }
  }
}

.input-invalid {
  background-color: rgba(255, 0, 0, 0.8);
}

.main-content {
  border-top-right-radius: 300px 250px;
  background-color: #fff;
  position: relative;
  z-index: 0;
  margin-top: -400px;

  background-image: url(../../img/bg/bg2.png), 
    url(../../img/bg/bg3.png),
    url(../../img/bg/bg2.png),
    url(../../img/bg/bg4.png);
  background-repeat: no-repeat, no-repeat, no-repeat, no-repeat;
  background-size: auto, auto, auto, auto;
  background-position: left 350px, right 610px, 105% 1800px, left 2300px;
}

.contact-form {
  margin: 0 auto;
  padding: 35px 35px 25px;
  background: url(../../img/bg/frame.svg) no-repeat 0 0;
  background-size: 100% 100%;
  border-radius: 32px;
  box-shadow: 2px 3px 5px 0px rgba(144,85,1,0.36);
  position: relative;
  z-index: 2;
  &__title {
    font-size: 25px;
    line-height: 36px;
    text-align: center;
    margin-bottom: 15px;
  }
  &__subtitle {
    font-size: 18px;
    line-height: 24px;
    text-align: center;
    color: #000000;
    margin-bottom: 25px;
    padding-left: 30px;
    padding-right: 30px;
  }
  & > form {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-left: 40px;
    padding-right: 40px;
  }
  &__label {
    align-self: flex-start;
    font-size: 14px;
    margin-bottom: 12px;
  }
  &__input {
    width: 100%;
    margin-bottom: 12px;
    & > input {
      border: 1px solid #111111;
      border-radius: 10px;
      padding: 20px;
      width: 100%;
      outline: none;
      transition: .25s;
      &:focus {
        box-shadow: 0 0 7px 2px rgba(141, 141, 141, 0.8);
      }
    }
  }
  &__btn {
    margin-top: 30px;
    margin-bottom: 15px;
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
    & > a {
      width: 100%;
      padding: 25px 0;
    }
  }
  &__policy {
    font-size: 11px;
    line-height: 12px;
    color: #888887;
    text-align: center;
    margin-bottom: 10px;
  }
  &__socials {
    font-size: 18px;
    color: #fcfcfc;
    display: flex;
    flex-direction: column;
    align-items: center;
    & > span {
      display: inline-block;
      margin-bottom: 30px;
      text-align: center;
    }
  }
  &--light-text &__title {
    font-size: 36px;
    line-height: 36px;
    color: #f9f7f7;
  }
  &--light-text &__subtitle {
    color: #fcfcfc;
  }
  &--light-text &__label {
    color: #ffffff;
  }
  &--light-text &__policy {
    color: #ffffff;
  }
  &--light-text &__input > input {
    border: none;
    &::placeholder {
      color: #d7d7d6;
    }
  }
}

.cirtificate-modal {
  position: fixed;
  z-index: 50;
  top: 0;
  left: 0;
  background-color: rgba(88, 88, 88, 0.55);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  & > img {
    height: 90%;
  }
}

.swiper {
  &-container {
    width: 100%;
    height: 360px;
  }
  &-slide {
    height: 100%;
    height: 360px;
  }
  &-button-prev, &-button-next {
    width: 42px;
    height: 35px;
  }
  &-button-prev {
    background: url(../../img/icon/arrow-left.png) no-repeat center / cover;
  }
  &-button-next {
    background: url(../../img/icon/arrow-right.png) no-repeat center / cover;

  }
}
