.header {
  width: 100%;
  color: #fff;
  padding-right: 60px;
  padding-left: 60px;
  padding-top: 12px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    display: flex;
    align-items: center;
  }
  &__logo-text {
    font-size: 15px;
    max-width: 150px;
  }
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__menu {}
  &__contacts {
    display: flex;
    align-items: center;
  }
  &__socials {
    margin-left: 60px;
  }
  &__call {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 25px;
  }
  &__tel {
    font-size: 30px;
    font-weight: 700;
    margin-bottom: 10px;
    &:hover {
      text-decoration: underline;
    }
  }
  &__hours {
    font-size: 15px;
  }
  &--fixed {
    transition: .5s;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 20;
    background-color: #13560d;
    padding-bottom: 12px;
    box-shadow: 0 2px 6px 1px rgba(19, 86, 13, 0.5);
  }
}

.header-menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__item {
    margin-left: 40px;
    transition: .25s;
    &:hover {
      color: rgba(204, 204, 204, 0.7);
    }
  }
}

.burger-btn {
  position: relative;
  z-index: 20;
  display: none;
  width: 60px;
  height: 60px;
  & > span {
    height: 2px;
    transition-property: height;
    transition-delay: .3s;
    width: 30px;
    position: absolute;
    left: 8px;
    top: 50%;
    background-color: #ffffff;
    &::before,
    &::after {
      content: '';
      position: absolute;
      width: 30px;
      height: 2px;
      background-color: #ffffff;
    }
  }
  &--disabled {
    & > span {
      &::before {
        animation: defaultBurgerBefore .6s forwards;
      }
      &::after {
        animation: defaultBurgerAfter .6s forwards;
      }
    }
  }
  &--active {
    & > span {
      height: 0;
      &::before {
        animation: rotateBurgerBefore .6s forwards;
      }
      &::after {
        animation: rotateBurgerAfter .6s forwards;
      }
    }
  }
}
@keyframes rotateBurgerBefore {
  0% {transform: translateY(-10px) rotate(0);}
  50% {transform: translateY(0px) rotate(0);}
  100% {transform: translateY(0px) rotate(45deg);}
}
@keyframes rotateBurgerAfter {
  0% {transform: translateY(10px) rotate(0);}
  50% {transform: translateY(0px) rotate(0);}
  100% {transform: translateY(0px) rotate(-45deg);}
}
@keyframes defaultBurgerBefore {
  0% {transform: translateY(0px) rotate(45deg);}
  50% {transform: translateY(0px) rotate(0);}
  100% {transform: translateY(-10px) rotate(0);}
}
@keyframes defaultBurgerAfter {
  0% {transform: translateY(0px) rotate(-45deg);}
  50% {transform: translateY(0px) rotate(0);}
  100% {transform: translateY(10px) rotate(0);}
}