.footer {
  padding-top: 25px;
  padding-bottom: 25px;
  padding-left: 100px;
  padding-right: 20px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    display: flex;
    & > img {
      margin-right: 35px;
    }
  }
  &__logo-text {
    font-size: 18px;
    color: rgb(254, 245, 89);
    max-width: 200px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

  }
  &__policy {
    font-size: 11px;
    line-height: 1.2;
    text-decoration: underline;
    text-decoration-color: rgba(254, 246, 89, 0.7);
  }
  &__info {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    font-size: 18px;
    line-height: 1.2;
    color: #fef559;
  }
}